ead div form input:nth-child(1) {\n\tbackground: none;\n\ttext-indent: 8px;\n}\n\n.head div form input:nth-child(2) {\n\tfloat: right;\n\twidth: 13%;\n\tpadding-bottom: 10px;\n\tbackground: url(../img/ss.png) no-repeat right 5px;\n\tcursor: pointer;\n}\n\n.head div.btn {\n\tmargin-top: 20px;\n\tposition: relative;\n}\n\n.head div.btn a {\n\tfloat: left;\n\tmargin: 0 10px;\n}\n\n.head ul {\n\tfloat: left;\n\tmargin-left: 18%;\n\tline-height: 50px;\n}\n\n.head ul li {\n\tfloat: left;\n\tpadding: 0 20px;\n}\n\n.head ul li>a {\n\tcolor: #262626;\n\tfont-size: 14px;\n\tdisplay: inline-block;\n\tposition: relative;\n}\n\n.head ul li a:before {\n\tcontent: \"\";\n\tbackground: #A10000;\n\tposition: absolute;\n\tbottom: -3px;\n\tz-index: -1;\n\ttransform: scaleX(0);\n\ttransition: all .5s ease;\n\t-webkit-transition: all .5s ease;\n}\n\n.head ul li:nth-child(1) a:before {\n\tleft: -12px;\n}\n\n.head ul li>a:hover {\n\tcolor: #A10000;\n}\n\n.head ul li>a:hover:before {\n\twidth: 56px;\n\theight: 2px;\n\ttransform: scaleX(1);\n\ttransition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);\n\t-webkit-transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);\n\t;\n\tz-index: 9999;\n}\n\n.head div.sList {\n\tposition: absolute;\n\ttop: 113px;\n\tleft: 0;\n\tz-index: 3;\n\tbackground: #fff;\n\twidth: 100%;\n\tdisplay: none;\n}\n\n.head div.sList div {\n\ttext-align: center;\n\tpadding: 20px 0;\n\tdisplay: flex;\n}\n\n.head div.sList div a {\n\tflex: 1 1 20%;\n\tborder-right: 1px solid #dbdbdb;\n}\n\n.head div.sList div a:last-child {\n\tborder-right: none;\n}\n\n.head div.sList div dl dt img {\n\twidth: 70%;\n}\n\n.head div.sList div dl dd {\n\tfont-size: 14px;\n}\n\n.head div.sList2 {\n\tdisplay: none;\n\tposition: absolute;\n\ttop: 113px;\n\tleft: 0;\n\tz-index: 3;\n\tbackground: #fff;\n\twidth: 100%;\n}\n\n.head div.sList2 div {\n\twidth: 46%;\n\tmargin: 0 auto;\n}\n\n.head div.sList2 a {\n\tfloat: left;\n\tmargin-right: 40px;\n}\n\n\n/*定位样式*/\n\n.ding {\n\tmargin-top: 0;\n\tposition: fixed;\n\ttop: 0;\n\tleft: 0;\n\twidth: 100%;\n\theight: 40px;\n\tpadding: 20px 0;\n\tbackground: #fff;\n\tz-index: 100;\n}\n\n.ding h1 {\n\tposition: absolute;\n\tleft: 10%;\n\ttop: 20%;\n}\n\n.ding h1 img {\n\twidth: 65%;\n}\n\n\n/*.ding #top1{\n\tdisplay: none;\n}*/\n\n.ding #top1 p,\n.ding #top1 div {\n\tdisplay: none;\n}\n\n.ding ul li a:before {\n\tbottom: -25px;\n}\n\n.ding div.sList,\n.ding div.sList2 {\n\ttop: 81px;\n}\n\n.ding ul {\n\tfloat: left;\n\tmargin-left: 18%;\n\tline-height: 0;\n}\n\n.ding div form {\n\twidth: 185px;\n\theight: 30px;\n\tline-height: 30px;\n\tmargin: 0 10px;\n\tmargin-top: 5px;\n\tborder-bottom: 1px solid #262626;\n}\n\n.ding div.sList2 a {\n\tfloat: left;\n\tmargin-right: 40px;\n\tline-height: 38px;\n}\n\n.ding div.sList div dl dd {\n\tline-height: 35px;\n}\n\n\n/*头部二维码*/\n\n.head div.btn p {\n\tdisplay: none;\n}\n\n.head div.btn p a {\n\twidth: 74px;\n\theight: 74px;\n\tposition: absolute;\n\ttop: 30px;\n\tbackground: #fff;\n\tright: 10px;\n\tpadding: 2px;\n\tz-index: 10;\n\tborder: 1px solid #A10000;\n}\n\n.head div.btn p a img {\n\twidth: 100%;\n}\n\n.head div.btn p a:before {\n\tposition: absolute;\n\ttop: -5px;\n\tleft: 50%;\n\twidth: 0;\n\theight: 0;\n\tmargin-left: -5px;\n\tvertical-align: middle;\n\tcontent: \" \";\n\tborder-right: 5px solid transparent;\n\tborder-bottom: 5px solid #A10000;\n\tborder-left: 5px solid transparent;\n}\n\n\n/*********************address********************/\n\n.address {\n\theight: 40px;\n\tline-height: 40px;\n\tborder-bottom: 1px solid #DBDBDB;\n}\n\n.address a,\n.address span {\n\tfloat: left;\n\tcolor: #777;\n}\n\n.address span {\n\tmargin: 0 20px;\n}\n\n.address a.on {\n\tcolor: #262626;\n}\n\n\n/*********************footer********************/\n\n.footer {\n\tmin-width: 1160px;\n}\n\n.footer .top {\n\tpadding: 30px 0;\n\tborder-top: 1px solid #DBDBDB;\n\tborder-bottom: 1px solid #DBDBDB;\n}\n\n.footer .top .wrapper {\n\tdisplay: flex;\n}\n\n.footer .top .wrapper div {\n\tflex: 1 1 25%;\n\tborder-right: 1px solid #dbdbdb;\n}\n\n.footer .top .wrapper div:last-child {\n\tborder-right: none;\n}\n\n.footer .top .wrapper div a img {\n\tdisplay: block;\n}\n\n.footer .top .wrapper div a {\n\tmargin-left: 25%;\n}\n\n.footer .top .wrapper div span {\n\tmargin-top: 15px;\n\tmargin-left: 10px;\n\tdisplay: block;\n\tfont-size: 16px;\n}\n\n.footer p.dibu {\n\tbackground: #000;\n\tpadding: 30px 0;\n\ttext-align: center;\n\tcolor: #fff;\n}\n\n\n/*********************gotop********************/\n\nbody {\n\tposition: relative;\n}\n\n.gotop {\n\tposition: fixed;\n\tright: 20px;\n\ttop: 400px;\n\tz-index: 20;\n}\n\n.gotop a {\n\tdisplay: block;\n\twidth: 48px;\n\theight: 48px;\n\tline-height: 38px;\n\tbackground: #f4f4f4;\n\tmargin-bottom: 10px;\n\tborder: 1px solid #d6d6d6;\n\tcursor: pointer;\n}\n\n.gotop dl dt {\n\tmargin: 0 auto;\n\twidth: 20px;\n\tpadding-top: 10px;\n}\n\n.gotop dl.goCart {\n\tposition: relative;\n}\n\n.gotop dl.goCart span {\n\tposition: absolute;\n\ttop: -9px;\n\tright: -8px;\n\twidth: 16px;\n\theight: 16px;\n\tborder-radius: 8px;\n\tbackground: #c10000;\n\tcolor: #fff;\n\ttext-align: center;\n\tline-height: 16px;\n}\n\n.gotop dl dd {\n\tdisplay: none;\n\tbackground: #a10000;\n\theight: 41px;\n\twidth: 41px;\n\tcolor: #fff;\n\tpadding: 8px 0 0 10px;\n\tline-height: 15px;\n}\n\n.gotop p {\n\tborder: 1px solid #A10000;\n\tpadding: 10px;\n\tposition: absolute;\n\tbackground: #f4f4f4;\n\ttop: 66px;\n\tleft: -130px;\n\tdisplay: none;\n}\n\n.gotop p:before {\n\tposition: absolute;\n\ttop: 50%;\n\tright: -6px;\n\twidth: 0;\n\tmargin-top: -5px;\n\theight: 0;\n\tmargin-left: -5px;\n\tvertical-align: middle;\n\tcontent: \" \";\n\tborder-bottom: 5px solid transparent;\n\tborder-left: 5px solid #a10000;\n\tborder-top: 5px solid transparent;\n}\n\n"],"sourceRoot":""}]);
